import React from 'react'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Carousel from './Carousel '
import './Home.css'
import {login} from './actionCreators'
import {connect} from 'react-redux'

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 使用时，<i className='iconfont icon-具体名字'></i>

const Home = (props) => {
    window.onload= function(){
        props.dispatch(login())
    }
    console.log(props)
    
    return(
        <div class='out'>
            <NavLink to='/home/searchpage'>
            <div class='search'>
                <div class='searchin'>
                    <div class='in1'></div>
                    <span>寻找宝贝店铺</span>
                </div>
            </div> 
            </NavLink>
            <Carousel />
            {   
                props.datalist.map((item,idx)=>(

                    <NavLink to={{pathname:'/home/details',index: idx}}>
                    <div class='left'>
                        <img src={item.img}/>
                        <span class='commodity'>{item.title}</span>
                        <span class='money'>￥{item.price}</span>
                    </div>
                </NavLink>
                ))
            } 
            <div class='last'></div> 
        </div>
        
    ) 
}

const mapStateToProps = (state) => ({
    datalist:state.home.datalist
})

export default connect(mapStateToProps)(Home);
