import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Collection from './taobao/Collection';
import Order from './taobao/Order';
import Mytb from './taobao/Mytb';
import Searchpage from './taobao/Searchpage'
import Details from './taobao/Details'
import Login from './taobao/Login'
import { Provider } from "react-redux";
import store from "./taobao/store";

const App = ()=>{
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/home/searchpage' component={Searchpage} />
				<Route path='/home/details' component={Details} />
				<Route path='/collection' component={Collection} />
				<Route path='/order' component={Order} />
				<Route path='/mytb' component={Mytb} />
				<Route path='/login' component={Login} />
				<Route render={()=><Redirect to='/'/>} />
			</Switch>
			<Mytab />
		</Router>
	</Provider>
}
export default App;