import React from 'react'
import { connect } from 'react-redux'

const Collection = (props) => {
    console.log(props)
    // var  length = props.collection.length;
    //console.log(length);
    var la = props.datalist.home.shoucang;
    function collection(){
        console.log(props.userinfo);
        if(props.userinfo.name=='张三'){
            props.dispatch({
                type:'collection',
            })
        }
        else{
            props.history.push('/login');
        }
    }
    collection();
    return (
        <div>
            {
                la.map((item,idx)=>(
                    <li key={idx} >
                        {item.title}
                        <button onClick={()=> {
                            props.dispatch({
                                type:'cncle',
                                index: idx,
                            })
                        }
                        }>取消收藏</button>
                    </li>
                ))
            }
        </div>
    )
}


const mapStateToProps = (state) => ({
    datalist:state,
    collection:state.collectionlist,
    userinfo:state.loginlist
})

export default connect(mapStateToProps)(Collection);