import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        if(pathname.split('/').length > 2){
            return null;
          }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="blue"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-icon4'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon4'></i>                           
                        }
                        title="收藏"
                        key="collection"
                        selected={ pathname === '/collection' }
                        onPress={ () => {
                            this.props.history.push('/collection')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan'></i>   
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/order' }
                        onPress={ () => {
                            this.props.history.push('/order');
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode'></i> 
                        }
                        selectedIcon={ 
                            <i className='iconfont icon-wode'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/mytb' }
                        onPress={ () => {
                            this.props.history.push('/mytb');
                        } }
                    >
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);