export const get = 'get'
export const collection = 'collection'
export const show = 'show'
export const login= 'login'
export const del = 'del'

export function get1(filter) {
  return { type: get, filter }
}

export function collection1(id) {
  return { type: collection, id}
}

export function show1(id) {
  return { type: show, id}
}

export function del1(id) {
  return { type: del, id}
}

export function login1(userinfo) {
  return { type: login, userinfo}
}