import {
  collection,
  show,
  del
} from './Action'

let shoucang=[
]


function collectlist(state =shoucang, action) {
  switch (action.type) {
    case collection:
      console.log(shoucang);
      shoucang.push({
        'id' : action.collectlist.id,
        'title' : action.collectlist.title,
      })
      return shoucang
    case show:
      console.log(action.collection_delete.id)
      return shoucang
    case del:
        let arr =[...state];
        arr.splice(action.collection_delete.id,1)
        console.log(action.collection_delete.id)
        return arr
    default:
      return state
    }
}

export default collectlist;