import React from 'react';
import { withRouter } from 'react-router-dom';
import './Details.css'
import {connect} from 'react-redux'
import { combineReducers } from 'redux';

const Details = (props) => {

    let line = props.datalistinfo;
    let idx = props.location.index;
    let id1 = line[idx].id;

    function collection(){
        console.log(props.userinfo);
        if(props.userinfo.name=='张三'){
            props.dispatch({
                type:'collection',
            })
        }
        else{
            props.history.push('/login');
        }
    }

    return (<div class='all'>
        <div class='ground' >
            <img 
                class='back'
                src={require('./png/左箭头.png')} 
                onClick={() =>{props.history.push('/')}}/>
            <img
                class='gouwu'
                src={require('./png/购物车.png')} />
        </div>

        <div>
            <img src={line[idx].img}/>
        </div>
        
        <div class='below'>
            <div class='tu'>
                <img src={require('./png/dianpu.png')} />
                <span>店铺</span>
            </div>
            <div class='tu'>
                <img src={require('./png/kefu.png')} />
                <span>客服</span>
            </div>
            <div class='tu1' onClick={collection}>
                <img 
                    id = {id1}
                    src={require('./png/xingxing.png')} 
                    onClick={() => {
                        //console.log('dgsfnh')
                        //console.log(props)
                        props.dispatch({
                            type:'clect',
                            index: idx,  
                        })
                    }}
                    
                />
                <span>收藏</span>       
            </div>
            <div class='shuangpin'>
                <div class='sleft'>
                    <span>加入购物车</span>
                </div>
                <div class='sright'>
                    <span>领劵购买</span>
                </div>
            </div>
        </div>
    </div>);
}


export default connect(
    state=>({  
        datalistinfo:state.home.datalist,
        collection:state.collectionlist,
        userinfo:state.loginlist
    })   
)(Details)