let datas = 
{

    datalist:[],

    shoucang:[],

    userinfo:'',

}

const home = (state = datas,action) =>{
    switch(action.type){
        case 'getlist':
            datas = Object.assign({},state,{
                datalist:[
                    ...state.datalist,
                    {
                        ...action.datalist
                    }
                ]
            });
            return datas;
        
        case 'clect':
            let lala = action.index;
            datas = Object.assign({},state,{
                shoucang:[
                    ...state.shoucang,{
                        ...state.datalist[lala]
                    }
                ]
            });
            return datas;

        case 'cncle':
            state.shoucang.splice(action.index,1)
            datas = Object.assign({},state,{
                shoucang:[
                    ...state.shoucang
                ]
            });
            return datas;
    }
    return datas;
}
export default home