import React, { Component } from 'react'
import {connect} from 'react-redux'
import store from './store'

function login(props){
    function getuserinfo(){
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login')
            .then(res=>res.json())
            .then(res=>{
                props.dispatch({
                    type:'login',
                    userinfo:{
                        name:res.data.name
                    }
                })
                props.history.push('/');
            })
    }
    return (
        <div>
            <div>
                <input type="text"/>
            </div>
            <div>
                <input type="password"/>
            </div>
            <div>
                <button onClick={getuserinfo}>登录</button>
            </div>
        </div>
    )
}
export default connect(
    state=>({  
        userinfo:state.loginlist
    })
)(login);