
const login = () =>{
    let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
    return (dispatch)=>{
        fetch(url, {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            for(var i=0;i<res.data.length; i++){
                dispatch({
                    type: 'getlist',
                    datalist:{
                        id: res.data[i].id,
                        img: res.data[i].img,
                        title: res.data[i].title,
                        price: res.data[i].price,
                    }
                 })     
            }
            
        })
    }

}


export {login}